$breakpoint-xs: 300px;
$breakpoint-s: 540px;
$breakpoint-mplus: 700px;

@mixin max-media($width) {
  @media only screen and (max-width: $width) {
    @content;
  }
}

@mixin min-media($width) {
  @media only screen and (min-width: $width) {
    @content;
  }
}

.intro__train-sign {
  position: absolute;
  z-index: 3;
  left: 15%;
  bottom: -0.2%;
  height: 50%;
  width: 14vh;

  @include min-media($breakpoint-mplus) {
    bottom: 0.5%;
    width: 19vh;
  }

  @include max-media($breakpoint-s) {
    width: 15.3vh;
    bottom: -2%;
  }

  @include max-media($breakpoint-xs) {
    bottom: -3%;
    width: 13.2vh;
  }
}
